import { title } from 'process';
import React, { useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import Post from './Post';

const UpdatePostForm = () => {
  const navigate = useNavigate();
  const { postId } = useParams();
  const [postTitle, setPostTitle] = useState('');
  const [postContent, setPostContent] = useState('');
  const [selectedImage, setSelectedImage] = useState<File | null>(null);

  useEffect(() => {
    // URL      
    const fetchPostData = async () => {
      try {
        const response = await fetch(`https://spartagameclub.shop/api/posts/${postId}`);
        if (response.ok) {
          const postData = await response.json();

          setPostTitle(postData.data.postTitle);
          setPostContent(postData.data.postContent);
        } else {
          console.error('    .');
        }
      } catch (error) {
        console.error(' : ', error);  
      }
    };

    fetchPostData();
  }, [postId]);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    switch (name) {
      case 'postTitle':
        setPostTitle(value);
        break;
      case 'postContent':
        setPostContent(value);
        break;
      default:
        break;
    }
  };

  const requestData = {
    postTitle: postTitle,
    postContent: postContent,
  };

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    setSelectedImage(file || null);
  };

  const handleSubmit = async (e: { preventDefault: () => void; }) => {
    e.preventDefault();

    const formData = new FormData();
    formData.append('requestDto', new Blob([JSON.stringify(requestData)], { type: "application/json" }));
    if (selectedImage) {
      formData.append('file', selectedImage);
    }

    try {
      const accessToken = localStorage.getItem('accessToken');
      const refreshToken = localStorage.getItem('refreshToken');

      if (!accessToken) {
        console.error('  .');
        alert(' ');
        navigate("/");
        return;
      }

      const response = await fetch(`https://spartagameclub.shop/api/posts/${postId}`, {
        method: 'PATCH',
        body: formData,
        headers: {
          'Access': `${accessToken}`,
          'Refresh': `${refreshToken}`,
        }
      }).then(async response => {
        if (response.ok) {
          const responseData = await response.json();
          console.log(responseData);
          const postId = responseData.data.postId;
          handleSuccess(postId);
        } else {
          handleFailure();
        }
      })

    } catch (error) {
      console.error(" " + error);
    }
  };

  const handleSuccess = (postId: number) => {
    console.log('  ');
    alert('  ');
    navigate(`/api/posts/${postId}`);
  };

  const handleFailure = () => {
    console.error('  ');
    alert('  ');
    window.location.reload()
  };

  const handleCancel = () => {
    navigate(-1);
  };

  return (
    <div className="container mt-5">
      {/*    */}
      <div className="mb-3 d-flex flex-column align-items-start">
        <label htmlFor="postTitle" className="form-label mb-2">
          
        </label>
        <input
          type="text"
          className="form-control mb-3"
          id="postTitle"
          value={postTitle}
          onChange={(e) => setPostTitle(e.target.value)}
          placeholder=" "
        />

        {/*   */}
        <label htmlFor="image" className="form-label me-2 mb-2">
           
        </label>
        <input
          type="file"
          className="form-control mb-3"
          id="image"
          accept="image/*"
          onChange={handleImageChange}
        />

        <label htmlFor="postContent" className="form-label mb-2">
          
        </label>
        <textarea
          className="form-control mb-3"
          id="postContent"
          value={postContent}
          onChange={(e) => setPostContent(e.target.value)}
          rows={4}
          placeholder=" "
        ></textarea>
      </div>
      <div className="mb-3 d-flex justify-content-center">
        <button type="button" className="btn btn-primary me-2" onClick={handleSubmit}>
          
        </button>
        <button type="button" className="btn btn-secondary" onClick={handleCancel}>
          
        </button>
      </div>
    </div>
  );
}

export default UpdatePostForm;
