import { createBrowserHistory } from "@remix-run/router";
import React, { useState } from "react";
import { Form, FormCheck, FormControl, InputGroup, Col, Row, Button } from "react-bootstrap";
import { Link } from "react-router-dom";

const history = createBrowserHistory();

function Signup() {
  const [isAdmin, setIsAdmin] = useState(false);
  const [formData, setFormData] = useState({
    email: "",
    nickname: "",
    password: "",
    checkPassword: "",
    isAdmin: false,
    adminToken: "",
    authNumber: ""
  });

  const handleChange = (e: { target: { name: any; value: any; }; }) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleAdminCheck = () => {
    setIsAdmin(!isAdmin);
  };

  //       
  const Confirm = async () => {
    try {
        const { authNumber, ...formDataWithoutAuthNumber } = formData;
        console.log("formData: formData", formData);
      const response = await fetch(`https://spartagameclub.shop/api/users/signup`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(formDataWithoutAuthNumber),
      });

      const responseBody = await response.json();
      console.log(responseBody);

      if (response.ok) {
        console.log(" !");
        alert(' ');
        history.push('/'); 
      } else {
        console.error(" ");
      }
    } catch (error) {
      console.error(" ", error);
    }
  };

  //  
  const Cancel = () => {
    console.log("!");
    history.push('/');
  };

  //  
  const EmailVerification = async () => {
    try {
      const response = await fetch(`https://spartagameclub.shop/api/users/signup/email`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ email: formData.email }),
      });

      if (response.ok) {
        console.log("   !");
        alert(' .');
      } else {
        console.error("   ");
      }
    } catch (error) {
      console.error(" ", error);
    }
  };    

  //  
  const EmailAuthCheck = async () => {
    try {
      console.log("EmailAuthCheck: formData", formData);
      const response = await fetch(`https://spartagameclub.shop/api/users/signup/emailauthcheck`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          email: formData.email,
          authNum: formData.authNumber,
        }),
      });

        const responseBody = await response.json();
        console.log(responseBody);

      if (response.ok) {
        console.log("  !");
        alert(' .')
      } else {
        console.error("  ");
        alert('  .  .')
      }
    } catch (error) {
      console.error(" ", error);
    }
  };

  return (
    <Row className="justify-content-center">
      <Col sm={4}>
        <h2 className="text-center mb-4"></h2>
        <Form>
          <Form.Group as={Row} className="mb-3">
            <Form.Label column sm={3}></Form.Label>
            <Col sm={8}>
              <InputGroup>
                <Form.Control type="text" name="email" placeholder=" ." onChange={handleChange}/>
                <Button variant="secondary" onClick={EmailVerification}>
                  
                </Button>
              </InputGroup>
            </Col>
          </Form.Group>

          <Form.Group as={Row} className="mb-3">
            <Form.Label column sm={3}></Form.Label>
            <Col sm={8}>
            <InputGroup>
                <Form.Control type="text" name="authNumber" placeholder="  ." onChange={handleChange} />
                <Button variant="secondary" onClick={EmailAuthCheck}>
                  
                </Button>
              </InputGroup>
            </Col>
          </Form.Group>

          <Form.Group as={Row} className="mb-3">
            <Form.Label column sm={3}></Form.Label>
            <Col sm={8}>
              <Form.Control type="text" name="nickname" placeholder=" , 4~10" onChange={handleChange} />
            </Col>
          </Form.Group>

          <Form.Group as={Row} className="mb-3">
            <Form.Label column sm={3}></Form.Label>
            <Col sm={8}>
              <Form.Control type="password" name="password" placeholder=" , 4~16" onChange={handleChange} />
            </Col>
          </Form.Group>

          <Form.Group as={Row} className="mb-3">
            <Form.Label column sm={3}> </Form.Label>
            <Col sm={8}>
              <Form.Control type="password" name="checkPassword" placeholder="    ." onChange={handleChange} />
            </Col>
          </Form.Group>

          <Form.Group as={Row} className="mb-3">
            <Col sm={{ offset: 4, span: 8 }}>
              <FormCheck
                type="checkbox"
                id="isAdmin"
                label=""
                name="isAdmin"
                checked={isAdmin}
                onChange={handleAdminCheck}
              />
            </Col>
          </Form.Group>

          {isAdmin && (
            <Form.Group as={Row} className="mb-3">
              <Form.Label column sm={3}> </Form.Label>
              <Col sm={8}>
                <InputGroup>
                  <FormControl
                    type="text"
                    name="adminToken"
                    placeholder="  ."
                  />
                </InputGroup>
              </Col>
            </Form.Group>
          )}

          <Row className="mb-3 justify-content-center">
            <Col sm={{ span: 4 }}>
              <Button variant="primary" className="me-2" onClick={Confirm}>
                
              </Button>
              <Button variant="secondary" onClick={Cancel}>
                
              </Button>
            </Col>
          </Row>
        </Form>
      </Col>
    </Row>
  );
}

export default Signup;
