import React, { useState } from 'react';
import { Button, Form } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';

interface Props {
  postId: string; //   ID
  getComments: (postId: string | undefined) => void;
}

const AddCommentForm: React.FC<Props> = ({ postId ,getComments}) => {
  const [commentContent, setCommentContent] = useState<string>('');
  const navigate = useNavigate();

  const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();

    const commentRequestDto = {
      content: commentContent,
      parentId: 0,
      accept: false
    };

    try {
      const accessToken = localStorage.getItem('accessToken');
      const refreshToken = localStorage.getItem('refreshToken');

      if (!accessToken || !refreshToken) {
        console.error('  .');
        alert(' ');
        navigate('/login');
        return;
      }

      const response = await fetch(`https://spartagameclub.shop/api/posts/${postId}/comments`, {
        method: 'POST',
        body: JSON.stringify(commentRequestDto),
        headers: {
          'Content-Type': 'application/json',
          'Access': accessToken,
          'Refresh': refreshToken,
        },
      });

      if (response.ok) {
        console.log('  ');
        alert('  ');
        getComments(postId);
      } else {
        console.error('  ');
        alert('  ');
      }
    } catch (error) {
      console.error(' ', error);
    }
  };

  const handleCancel = () => {
    navigate(-1);
  };

  return (
      <Form onSubmit={handleSubmit}>
        <Form.Group controlId="commentContent">
          <Form.Label> </Form.Label>
          <Form.Control
              as="textarea"
              rows={3}
              value={commentContent}
              onChange={(e) => setCommentContent(e.target.value)}
              placeholder="  ."
          />
        </Form.Group>
        <Button variant="primary" type="submit">
           
        </Button>
        <Button variant="secondary" onClick={handleCancel}>
          
        </Button>
      </Form>
  );
};

export default AddCommentForm;
