import React, { useEffect } from 'react';

const Logout = () => {
  useEffect(() => {
    const handleLogout = async () => {
      try {
        const accessToken = localStorage.getItem('accessToken');

        //   
        const isTokenExpired = isExpired(accessToken);

        if (isTokenExpired) {
          //   ,       
          window.location.replace('/login');
          return;
        }

        //      
        const response = await fetch('https://spartagameclub.shop/api/users/logout', {
          method: 'DELETE',
          headers: {
            'Access': `${accessToken}`,
          },
        });

        if (response.ok) {
          //    
          localStorage.removeItem('accessToken');

          //       
          window.location.replace('/front-end');
        } else {
          //     
          console.error(' ');
        }
      } catch (error) {
        //  
        console.error('    ', error);
      }
    };

    //     
    handleLogout();
  }, []);

  //     
  const isExpired = (token: string | null) => {
    if (!token) {
      //      
      return true;
    }
  
    try {
      //    
      const payload = JSON.parse(atob(token.split('.')[1]));
  
      //  (exp)    
      const currentTimestamp = Math.floor(Date.now() / 1000); //   
      return payload.exp < currentTimestamp;
    } catch (error) {
      console.error('       ', error);
      return true; //       
    }
  };

  return (
    <div>
      <p> ...</p>
    </div>
  );
};

export default Logout;
