import axios from 'axios';
import React, { useState } from 'react';
import { Form, FormControl, InputGroup, Col, Row, Button } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';

// Import createBrowserHistory from '@remix-run/router' is not used in this component

function Login() {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    email: '',
    password: '',
  });

  const handleChange = (e: { target: { name: any; value: any; }; }) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  //  : 
  const Confirm = async () => {
    try {
      console.log('formData:', formData);

      const response = await fetch(`https://spartagameclub.shop/api/users/login`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          email: formData.email,
          password: formData.password,
        }),
      });

      console.log(response);

      if (response.ok) {
        const refresh = response.headers.get('refresh');
        const access = response.headers.get('access');
        localStorage.setItem('accessToken', access ? access : '');
        localStorage.setItem('refreshToken', refresh ? refresh : '');

        console.log(await response.json()); // Logging the JSON response

        console.log(' ');
        navigate('/');
        window.location.reload();
      } else {
        console.error(' ');
        alert(' ,  .');
      }
    } catch (error) {
      console.error('   :', error);
    }
  };

  //   : 
  const Cancel = () => {
    console.log('');
    navigate('/');
  };

  // 
  const handleKakaoLogin = async () => {
    // Kakao OAuth   
    window.location.href = `https://kauth.kakao.com/oauth/authorize?client_id=${process.env.REACT_APP_KAKAO_API_KEY}&redirect_uri=${process.env.REACT_APP_API_BASE_URL}&response_type=code`;
  };

  return (
    <Row className="justify-content-center">
      <Col sm={4}>
        <h2 className="text-center mb-4"></h2>
        <Form>
          <Form.Group as={Row} className="mb-3">
            <Form.Label column sm={3}>
              
            </Form.Label>
            <Col sm={8}>
              <InputGroup>
                <FormControl type="text" name="email" placeholder=" ." onChange={handleChange} />
              </InputGroup>
            </Col>
          </Form.Group>

          <Form.Group as={Row} className="mb-3">
            <Form.Label column sm={3}>
              
            </Form.Label>
            <Col sm={8}>
              <InputGroup>
                <FormControl type="password" name="password" placeholder=" ." onChange={handleChange} />
              </InputGroup>
            </Col>
          </Form.Group>

          <Row className="mb-3 justify-content-center">
            <Col sm={{ span: 4 }}>
              <Button variant="primary" className="me-2" onClick={Confirm}>
                
              </Button>
              <Button variant="secondary" onClick={Cancel}>
                
              </Button>
            </Col>
          </Row>
          <Row className="mb-3 justify-content-center">
            <Col sm={{ span: 4 }}>
              <Button variant="warning" onClick={handleKakaoLogin}>
                 
              </Button>
            </Col>
          </Row>
        </Form>
      </Col>
    </Row>
  );
}

export default Login;
